  width: 100%;
  min-height: 36px;
  height: auto;
  background-color: #fff;
  border: 1px solid #e8e8e8;
  border-radius: 3px;
  margin: 0 10px 0 0;
  padding: 7px 12px;
  font-size: 14px;
  line-height: 22px;
